<template>
  <div id="divHomepage">

    <mt-header fixed :title=name>
      <router-link to="/" slot="left">
        <mt-button icon="back">Back</mt-button>
      </router-link>
    </mt-header>

    <mt-cell v-for="(item, index) in items" v-bind:title=item.name>
      <span>{{item.alias}}</span>
      <img slot="icon" src="../assets/staff.png">
    </mt-cell>


  </div>
</template>


<script>
export default {
  data() {
    return {
      name: "Staff",
      items: [
        { name: "Supervisor I", alias: "SUPV" },
        { name: "Technician I", alias: "TECH" },
        { name: "Tom ZZ", alias: "TOM" },
        { name: "Jerry Xy", alias: "JERRY" }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 40px;
  height: 40px;
}  
</style>



