<template>
  <div id="divHomepage">

    <mt-header fixed :title=name>
      <router-link to="/" slot="left">
        <mt-button icon="back">Back</mt-button>
      </router-link>
    </mt-header>

    <mt-cell v-for="(item, index) in items" v-bind:title=item.name>
      <mt-badge type="success">{{index*2}}</mt-badge>
      <img slot="icon" src="../assets/escalation.png">
    </mt-cell>


  </div>
</template>


<script>
export default {
  data() {
    return {
      name: "Escalation",
      items: [
        { name: "High", alias: "ES" },
        { name: "Normal", alias: "NE" },
        { name: "Low", alias: "VC" },
        { name: "N/A", alias: "NW" }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 40px;
  height: 40px;
}  
</style>



