<template>
  <div id="divHomepage">

    <mt-header fixed :title=name>
      <router-link to="/" slot="left">
        <mt-button icon="back">Back</mt-button>
      </router-link>
    </mt-header>

    <mt-checklist

      v-model="value"
      :options=items>
    </mt-checklist>


  </div>
</template>


<script>
export default {
  data() {
    return {
      name: "Ranking",
      value: ["G", "S", "D"],
      items: [
        { label: "Gold", value: "G" },
        { label: "Silder", value: "S" },
        { label: "Pink", value: "P" },
        { label: "Diamond", value: "D" }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 40px;
  height: 40px;
}
</style>



