<template>
  <div id="divHomepage">

    <mt-header fixed :title=name>
      <router-link to="/" slot="left">
        <mt-button icon="back">Back</mt-button>
      </router-link>
    </mt-header>
    <mt-cell title="Change Password"></mt-cell>
    <mt-field label="Old" placeholder="Old password" type="password" v-model="password"></mt-field>
    <mt-field label="New" placeholder="New password" type="password" v-model="pwd1"></mt-field>
    <mt-field label="New" placeholder="New password again" type="password" v-model="pwd2"></mt-field>


  </div>
</template>


<script>
export default {
  data() {
    return {
      name: "Change Password",
      password: '',
      pwd1: "",
      pwd2: ""
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 40px;
  height: 40px;
}
</style>



