<template>
  <div id="divHomepage">
    <mt-header fixed title="Configurations"></mt-header>

    <div class="center">
      <h3>{{ msg }}</h3>
    </div>

    <mt-cell title="Manage Staff" is-link to="/staff">
      <img slot="icon" src="../assets/staff.png">
    </mt-cell>
    <mt-cell title="Sections" is-link to="/sections">
      <img slot="icon" src="../assets/sections.png">
    </mt-cell>
    <mt-cell title="Escalation" is-link to="/escalation">
      <img slot="icon" src="../assets/escalation.png">
    </mt-cell>
    <mt-cell title="Standard Task Types" is-link to="/tasks">
      <img slot="icon" src="../assets/task.png">
    </mt-cell>
    <mt-cell title="Custom Task Types" is-link to="/tasks">
      <img slot="icon" src="../assets/select.png">
    </mt-cell>
    <mt-cell title="Player Ranking" is-link to="/ranking">
      <img slot="icon" src="../assets/card.png">
    </mt-cell>
    <mt-cell title="Configuration" is-link to="/config">
      <img slot="icon" src="../assets/configuration.png">
    </mt-cell>
    <mt-cell title="Change Password" is-link to="/changepwd">
      <img slot="icon" src="../assets/lock.png">
    </mt-cell>

    <br>
    <br>
    <br>
    <mt-tabbar fixed onclick="console.log('hi')">
      <mt-tab-item>
        <icon name="list" flip="horizontal"></icon>
        <br>
        Configurations
      </mt-tab-item>
      <mt-tab-item>
         <icon name="times-circle" flip="horizontal"></icon>
         <br>
        Logout
      </mt-tab-item>
    </mt-tabbar>

  </div>
</template>

<script>
export default {
  name: "Homepage",
  data() {
    return {
      msg: "Welcome, iosadmin"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


#divHomepage img {
  width: 60px;
  height: 60px;
}

#divHomepage .mint-cell-wrapper {
  padding: 30px;
  padding-left: 0;
}

#divHomepage .mint-cell {
  margin-top: 10px;
}
</style>



