<template>
  <div id="divHomepage">

    <mt-header fixed :title=name>
      <router-link to="/" slot="left">
        <mt-button icon="back">Back</mt-button>
      </router-link>
    </mt-header>

    <mt-cell v-for="(item, index) in items" v-bind:title=item.name>
      <mt-switch v-model="item.value"></mt-switch>
      <img slot="icon" src="../assets/task.png">
    </mt-cell>


  </div>
</template>


<script>
export default {
  data() {
    return {
      name: "Tasks",
      items: [
        { name: "Drink", alias: "ES", value: true },
        { name: "Call Taxi", alias: "NE", value: true },
        { name: "MEAL", alias: "VC", value: false },
        { name: "Welcome", alias: "NW", value: true }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 40px;
  height: 40px;
}
</style>



