<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
      <!-- <h1>{{ message }}</h1> -->
    <router-view/>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  name: "app",
  data() {
    return {
      message: "..."
    };
  }
};

setInterval(() => {
  Toast({
    message: "This is only a demo site",
    position: "bottom",
    duration: 3000
  });
}, 100 * 1000);
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 60px;
}

body {
  font-family: verdana, arial, sans-serif;
  /*font-family: verdana;*/
  font-weight: normal;
  text-align: left;
  background-color: #f3f3f3;
  color: #136dac;
}

a,
a:hover {
  text-decoration: none;
}

hr {
  border-bottom-width: 0;
  color: #bbbbbb;
}

#app .mint-header {
  background-color: #136dac;
  color: white !important;
  font-weight: bold !important;
}
#app .mint-header .mint-header-title {
  background-color: #136dac;
  color: white;
  font-weight: bold;
  margin: 0;
  line-height: 30px;
}

#app .mint-tab-item{
  padding: 0;
  margin: 0;
}

#app .mint-tab-item-label {
  background-color: #136dac;
  color: white;
  padding: 8px;
  margin: 0;
  line-height: 15px;
}

#divHomepage .mint-cell-wrapper {
  padding-left: 0;
}

.mint-header .mint-button {
  color: white;
}

.center {
  text-align: center;
}
</style>



